const extAwsBaseUrl = process.env.REACT_APP_API_ROOT
const extSubsBaseUrl = process.env.REACT_APP_API_SUBS
const extCitiesBaseUrl = process.env.REACT_APP_API_CITIES
const extContactBaseUrl = process.env.REACT_APP_API_OLIV
const emailBaseUrl = process.env.REACT_APP_API_EMAIL

export const endPoints = {
  getPaymentIntent: extSubsBaseUrl + '/get_setup_intent',
  addPromoCode: extSubsBaseUrl + '/get_promo_details',
  getPackages: extSubsBaseUrl + '/get_packages',

  paymentSubmit: extSubsBaseUrl + '/subscription/subscribe',
  cardUpdateSubmit: extSubsBaseUrl + '/subscription/card/update',
  createCareerProUserInfo: extAwsBaseUrl + '/career-pro/create',
  careerProCheckoutSubmitted: extAwsBaseUrl + '/career-pro/set-is-completed',
  getCities: extCitiesBaseUrl + '/get-cities',
  contactForm: extContactBaseUrl + '/contact-us',
  emailCareerPro: emailBaseUrl + '/oliv/emails/career_pro',
  cancelEmailCareerPro: emailBaseUrl + '/oliv/emails/career_pro_closed'

}



